// flex helpers

.flex {

   display: flex;

   > *.full {
       flex: 1;
   }
   &.column {
       flex-direction: column;
   }
   &.justify-center {
       justify-content: center;
   }
   &.align-center {
       align-items: center;
   }
   &.space-between {
    justify-content: space-between;
   }
   &.space-around {
    justify-content: space-around;
   }
}

.bg-good{
    background-color: $good !important;
}

.bg-bad{
    background-color: $bad !important;
}